export function formatShowTime(date){
  if(date == null || date == 0){
    return ''
  }else{
    let time = new Date(date*1000);
    let y = time.getFullYear();
    let M = time.getMonth();
    let D = time.getDate();
    let h = time.getHours();
    let m = time.getMinutes();
    return y+'-'+M+'-'+D+' '+h+':'+m
  }

}