import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Transport from '../views/Transport.vue'
import Newway from '../views/Newway.vue'
import NewbillDetail from '../views/NewbillDetail.vue'
import Yard from '../views/Yard.vue'
import Damage from '../views/Damage.vue'
import Me from '../views/Me.vue'
import MoveInside from '../views/MoveInside.vue'
import Deliver from '../views/Deliver.vue'
import Washbox from '../views/Washbox.vue'
import Charge from '../views/Charge.vue'
import Oil from '../views/Oil.vue'
import NewDamage from '../views/NewDamage.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: Home,
    meta: {
      showTab: true
    }
  },
  {
    path: '/transport',
    name: 'transport',
    component: Transport,
    meta: {
      showTab: true
    }
  },
  {
    path: '/yard',
    name: 'yard',
    component: Yard,
    meta: {
      showTab: true
    }
  },
  {
    path: '/damage',
    name: 'damage',
    component: Damage,
    meta: {
      showTab: true
    }
  },
  {
    path: '/me',
    name: 'me',
    component: Me,
    meta: {
      showTab: true
    }
  },
  {
    path: '/newway',
    name: 'newway',
    component: Newway,
    meta: {
      showTab: false
    }
  },
  {
    path: '/newbillDetail',
    name: 'newbillDetail',
    component: NewbillDetail,
    meta: {
      showTab: false
    }
  },
  {
    path: '/moveInside',
    name: 'moveInside',
    component: MoveInside,
    meta: {
      showTab: false
    }
  },{
    path: '/deliver',
    name: 'deliver',
    component: Deliver,
    meta: {
      showTab: false
    }
  },{
    path: '/washbox',
    name: 'washbox',
    component: Washbox,
    meta: {
      showTab: false
    }
  },{
    path: '/charge',
    name: 'charge',
    component: Charge,
    meta: {
      showTab: false
    }
  },{
    path: '/oil',
    name: 'oil',
    component: Oil,
    meta: {
      showTab: false
    }
  },{
    path: '/newDamage',
    name: 'newDamage',
    component: NewDamage,
    meta: {
      showTab: false
    }
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
