<template>
  <div id="new-damage">
    <van-nav-bar class="top"
      fixed
      title=""
      left-text=""
      :right-text="showAdd?'':''"
      left-arrow
      @click-left="goBack"
      @click-right="change"
    />
    <div class="add" v-show="showAdd">
      <van-field v-model="discover_station" required center clearable right-icon="question-o" label="" placeholder="" @click-right-icon="$toast('')">
        <van-button slot="button" :disabled="discover_station.length>1?false:true" size="small" type="primary" is-link @click="choiceHandleStation"></van-button>
      </van-field>
      <van-field
        v-model="discover_time"
        disabled
        label=""
        placeholder=""
        @click="choiceOperationTime"
      />
      <van-field v-model="box_num" required clearable label="" placeholder="" right-icon="qr" @click-right-icon="startRecognize()"/>
      <div class="van-cell van-cell--center van-field">
        <div class="van-cell__title van-field__label">
          <span></span>
        </div>
        <div class="van-cell__value">
          <van-checkbox  name="item.tab"  v-model="item.active" v-for="item in damageStyles" :key="item.tab">{{item.tab}}</van-checkbox >
        </div>
      </div>
      <h3 class="field_title" v-show="img_list.length>0"></h3>
      <div class="img-list">
        <div v-for="i in img_list" :key="i.img_id">
          <img :src="'http://192.168.31.62:81'+i.img_path" alt="">
          <van-icon name="clear" @click="deleteImg(i.img_id)" />
        </div>
      </div>
      <h3 class="field_title"></h3>
      <van-uploader :accept="'image/*'" v-model="img_info" capture="camera" multiple/>
      <van-field
        v-model="remark"
        rows="1"
        autosize
        label=""
        type="textarea"
        placeholder=""
      />
    </div>
    <div class="show" v-show="!showAdd">
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{discover_station}}</span>
      </div>
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{discover_time}}</span>
      </div>
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{box_num}}</span>
      </div>    
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{damage_style_show}}</span>
      </div>     
      <h3 class="field_title"></h3>
      <div class="img-list">
        <div v-for="i in img_list" :key="i.img_id">
          <img :src="'http://192.168.31.62:81'+i.img_path" alt="">
        </div>
      </div>
      <div class="infoItem">
        <span></span>
        <span>{{remark}}</span>
      </div> 
    </div>
    <div class="box-op">
      <van-button plain hairline round size="mini" type="danger" @click="deleteYard"></van-button>
    </div>
    <!--  -->
    <div :class="[showScan ? 'toTop':'', 'scan']">
      <div id="bcid">
        <van-nav-bar class="top"
          fixed
          title=""
          left-text=""
          left-arrow
          @click-left="cancel"
        />
        <div style="height:40%"></div>
        <p class="tip">......</p>
      </div>
    </div>
    <!--  -->
    <van-popup v-model="showHandleStation" position="bottom">
      <van-picker
        :columns="discover_stations"
        show-toolbar
        title=""
        @cancel="cancelHandleStation"
        @confirm="confirmHandleStation" />
    </van-popup>
    <!-- -->
    <van-popup v-model="showOperationTime" position="bottom">
      <van-datetime-picker
        v-model="operationTime"
        type="datetime"
        @cancel="cancelOperationTime"
        @confirm="confirmOperationTime"
      />
    </van-popup>
  </div>
</template>

<script>
  let scan = null
  import stationList from '../js/stationData.js';
  import {formatShowTime } from '../js/formatShowTime';
  import {formatDate } from '../js/formattime.js';
  export default {
    data() {
      return {
        list_id: '',
        showAdd: false, //
        stationList: stationList, //
        discover_station: '', //
        discover_stations: [], //
        showHandleStation: false, //
        discover_time: '', //
        operationTime: new Date(), //
        showOperationTime: false, //
        box_num: 'TBJU190', //
        remark: '', //
        img_info: [], //
        img_list: [], //
        damageStyles: [],//
        damage_style: '',
        damage_style_show: '',
        showScan: false
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      goBack() {
        history.go(-1)
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min  
      },
      change(){
        if(this.showAdd == false){
          this.showAdd = true
        }else{
          this.saveTravel()
        }
      },
      deleteImg(id){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/delete_img',{
            channel: 'app',
            img_id: id
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == 200){
              this.$toast('');
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }
          }).catch(err => {
            console.log(err)
          })
        })
      },
      // 
      choiceHandleStation(){
        this.discover_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.discover_station.toUpperCase()) != -1){
            this.discover_stations.push(v);
          }
        })
        this.showHandleStation = true;
      },
      cancelHandleStation(){
        this.showHandleStation = false;
      },
      confirmHandleStation(value){
        if(value.length != 0){
          this.discover_station = value.text;
        }
        this.showHandleStation = false;
      },
      // 
      choiceOperationTime(){
        this.showOperationTime = true
      },
      cancelOperationTime(){
        this.showOperationTime = false
      },
      confirmOperationTime(){
        this.showOperationTime = false;
        this.discover_time = this.timeFormat(this.operationTime)
      },
      saveTravel(){
        if(this.discover_station == '' || this.box_num == ''){
          this.$dialog({
            title: '',
            message: ''
          });
        }else{
          if(this.img_info.length == 0 && this.img_list.length == 0){
            this.$toast('');
          }else{
            this.damage_style = '';
            this.damageStyles.map((v,i) => {
              if(v.active == true){
                this.damage_style += (i+1)+','
              }
            })
            var formData = new FormData();
            formData.append('channel', 'app')
            formData.append('list_id', this.list_id)
            formData.append('list_type', '6')
            formData.append('box_num', this.box_num)
            formData.append('discover_station', this.discover_station)
            formData.append('discover_time', this.discover_time)
            formData.append('damage_style', this.damage_style)
            formData.append('remark', this.remark)
            if(this.img_info.length!=0){
              for(var i=0;i<this.img_info.length;i++){
                formData.append('file'+i,this.img_info[i].file,this.img_info[i].file.name)
              }
            }
            this.$file.post('http://192.168.31.62:81/information/save_box_damage',formData,{
              headers: {
                'Content-Type': 'multipart/form-data',
                'Token': localStorage.getItem('token'),
              }
            }).then(res => {
              if(res.data.code == 200){
                this.$toast('');
                this.list_id = res.data.result;
                this.getOperationDetail();
                this.showAdd = false
              }else if(res.data.code == '401'){
                this.$dialog.alert({
                  title: '',
                  message: res.data.reason
                })
                this.$router.push({
                  path: './me'
                })
              }else{
                this.$dialog.alert({
                  title: '',
                  message: ''+res.data.reason
                })
              }
            }).catch(err => {
              console.log(err)
            })
          }
        }
      },
      getDamageStyle(){
        this.$http.post('http://192.168.31.62:81/information/damage_style',{
          channel: 'app'
        },{
          headers: {
            'Token': localStorage.getItem('token'),
          }
        }).then(res => {
          if(res.data.code == 200){
            var styles = res.data.result.split(',');
            styles.map((v,i) => {
              this.damageStyles.push({
                'active': false,
                'tab': v
              })
            })
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      getOperationDetail(){
        this.$http.post('http://192.168.31.62:81/information/box_damage_list_details',{
          channel: 'app',
          list_id: this.list_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            this.damage_style_show = '';
            this.img_list = res.data.result.img;
            this.box_num = res.data.result.box[0].box_num
            this.remark = res.data.result.box[0].remark
            this.discover_station = res.data.result.operation[0].handle_station
            this.discover_time = formatShowTime(res.data.result.operation[0].operation_time);
            var damageArr = res.data.result.box[0].damage_style.split(',');
            damageArr.map(v => {
              if(v == damageArr.length-1){
                this.damage_style_show += this.damageStyles[v-1].tab
              }else{
                this.damage_style_show += this.damageStyles[v-1].tab+','
              }
              this.damageStyles[v-1].active = true
            })
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      cancel(){
        this.showScan = false;
        this.closeScan()
        this.cancelScan()
        scan = null
      },
      // 
      startRecognize () {     
        this.showScan = true;   
        let that = this
        if (!window.plus) return
        scan = new plus.barcode.Barcode('bcid')
        scan.onmarked = onmarked
        scan.start()
        function onmarked (type, result, file) {
          switch (type) {
            case plus.barcode.QR:
              type = 'QR'
              break
            case plus.barcode.EAN13:
              type = 'EAN13'
              break
            case plus.barcode.EAN8:
              type = 'EAN8'
              break
            default:
              type = '' + type
              break
          }
          result = result.replace(/\n/g, '')
          that.box_num = result
          that.showScan = false;
          that.closeScan()
          that.cancelScan()
          scan = null
        }
      },
      // 
      startScan () {        
        if (!window.plus) return
        scan.start()
      },
      // 
      cancelScan () {
        if (!window.plus) return
        scan.cancel()
      },
      // 
      closeScan () {
        if (!window.plus) return
        scan.close()
      },
      // 
      deleteYard(){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/operation_list_delete',{
            channel: 'app',
            list_id: this.list_id
          },{
             headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('');
              this.$router.push({
                path: './transport'
              })
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './yard'
              })
            }else{
              this.$dialog.alert({
                title: '',
                message: ''+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          });
        }).catch()
      }
    },
    mounted () {
      this.list_id = this.$route.query.list_id
      if(this.list_id == undefined){
        this.list_id = '';
        this.showAdd = true
      }else{
        this.getOperationDetail()
      }
      this.getDamageStyle();
    },
  }
</script>

<style scoped>
#new-damage{
  width: 100%;
  min-height: 100%;
  padding-top: 46px;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top *{
  color: white!important;
}
.field_title{
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  background-color: white;
}
/*  */
.van-uploader{
  padding: 10px 16px;
}
.img-list{
  display: flex;
  padding: 10px 16px;
}
.img-list>div{
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 8px 8px 0;
}
.img-list>div img{
  width: 100%;
  height: 100%;
}
.img-list>div i{
  top: -8px;
  right: -8px;
  position: absolute;
  color: #969799;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
/*  */
.infoItem{
  border-top: 1px solid #eeeeee;
  font-size: 12px;
  padding: 0 10px;
  color: #555555;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  background-color: white;
}
/*  */
.scan {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}
.toTop{
  z-index: 100000;
}
.scan #bcid {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 3rem;
  text-align: center;
  color: #fff;
  background: #ccc;
}
#bcid .top{
  z-index: 1000000!important;
}
.scan footer {
  position: absolute;
  left: 0;
  bottom: 1rem;
  height: 2rem;
  line-height: 2rem;
  z-index: 2;
}
</style>