<template>
  <div id="washBox">
    <van-nav-bar class="top"
      fixed
      title=""
      left-text=""
      :right-text="showAdd?'':''"
      left-arrow
      @click-left="goBack"
      @click-right="change"
    />
    <div class="add" v-show="showAdd">
      <van-field v-model="handle_station" required center clearable right-icon="question-o" label="" placeholder="" @click-right-icon="$toast('')">
        <van-button slot="button" :disabled="handle_station.length>1?false:true" size="small" type="primary" is-link @click="choiceHandleStation"></van-button>
      </van-field>
      <van-field
        v-model="operation_time"
        disabled
        label=""
        placeholder=""
        @click="choiceWashingBoxTime"
      />
      <h3 class="field_title" v-show="img_list.length>0"></h3>
      <div class="img-list">
        <div v-for="i in img_list" :key="i.img_id">
          <img :src="'http://192.168.31.62:81'+i.img_path" alt="">
          <van-icon name="clear" @click="deleteImg(i.img_id)" />
        </div>
      </div>
      <h3 class="field_title"></h3>
      <van-uploader :accept="'image/*'" v-model="img_info" capture="camera" multiple/>
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.box_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
            <van-icon name="edit" class="edit" @click="editList(i)"/>
            <van-icon name="cross" class="box-list-delete" @click="deleteList(i)" />
          </span>
        </div> 
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.abnormal_situation}}</span>
        </div>   
      </div>
      <div class="add-box">
        <h3 class="field_title"></h3>
        <van-field v-model="box_num" required clearable label="" placeholder="" right-icon="qr" @click-right-icon="startRecognize()"/>
        <van-field
          v-model="abnormal_situation"
          rows="1"
          autosize
          label=""
          type="textarea"
          placeholder=""
        />
        <div class="box-op">
          <van-button plain hairline round size="mini" type="danger" @click="addBox"></van-button>
        </div> 
      </div>
    </div>
    <div class="show" v-show="!showAdd">
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{handle_station}}</span>
      </div>
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{operation_time}}</span>
      </div>
      <h3 class="field_title"></h3>
      <div class="img-list">
        <div v-for="i in img_list" :key="i.img_id">
          <img :src="'http://192.168.31.62:81'+i.img_path" alt="">
        </div>
      </div>
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.box_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
          </span>
        </div> 
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.abnormal_situation}}</span>
        </div>   
      </div>
    </div>
    <!--  -->
    <div :class="[showScan ? 'toTop':'', 'scan']">
      <div id="bcid">
        <van-nav-bar class="top"
          fixed
          title=""
          left-text=""
          left-arrow
          @click-left="cancel"
        />
        <div style="height:40%"></div>
        <p class="tip">......</p>
      </div>
    </div>
    <!--  -->
    <van-popup v-model="showHandleStation" position="bottom">
      <van-picker
        :columns="handle_stations"
        show-toolbar
        title=""
        @cancel="cancelHandleStation"
        @confirm="confirmHandleStation" />
    </van-popup>
    <!--  -->
    <van-popup v-model="showWashingBoxTime" position="bottom">
      <van-datetime-picker
        v-model="washingBoxTime"
        type="datetime"
        @cancel="cancelWashingBoxTime"
        @confirm="confirmWashingBoxTime"
      />
    </van-popup>
  </div>
</template>

<script>
  let scan = null;
  import {formatShowTime } from '../js/formatShowTime';
  import {formatDate } from '../js/formattime.js';
  import stationList from '../js/stationData.js';
  export default {
    data() {
      return {
        list_id: '',
        showAdd: false, //
        stationList: stationList, //
        handle_station: '', //
        handle_stations: [], //
        showHandleStation: false, //
        operation_time: '', //
        washingBoxTime: new Date(), //
        showWashingBoxTime: false, //
        box_id: '',
        box_num: 'TBJU190', //
        abnormal_situation: '', //
        box_info: [],
        img_info: [], //
        img_list: [],
        showScan: false
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      goBack() {
        history.go(-1)
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min  
      },
      change(){
        if(this.showAdd == false){
          this.showAdd = true
        }else{
          this.saveTravel()
        }
      },
      showDetail(item){
        this.box_info.find(v=>{v.box_num == item.box_num ? v.show_detail = !v.show_detail : ''})
      },
      editList(item){
        this.box_id = item.box_id
        this.box_num = item.box_num
        this.showWashingBoxTime = item.showWashingBoxTime
        this.box_info.map((v,i)=>{
          if(v.box_num == item.box_num){
            this.box_info.splice(i,1)
          }
        })
      },
      deleteImg(id){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/delete_img',{
            channel: 'app',
            img_id: id
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == 200){
              this.$toast('');
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }
          }).catch(err => {
            console.log(err)
          })
        })
      },
      // 
      choiceHandleStation(){
        this.handle_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.handle_station.toUpperCase()) != -1){
            this.handle_stations.push(v);
          }
        })
        this.showHandleStation = true;
      },
      cancelHandleStation(){
        this.showHandleStation = false;
      },
      confirmHandleStation(value){
        if(value.length != 0){
          this.handle_station = value.text;
        }
        this.showHandleStation = false;
      },
      // 
      choiceWashingBoxTime(){
        this.showWashingBoxTime = true
      },
      cancelWashingBoxTime(){
        this.showWashingBoxTime = false
      },
      confirmWashingBoxTime(){
        this.showWashingBoxTime = false;
        this.operation_time = this.timeFormat(this.washingBoxTime)
      },
      deleteList(item){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.box_info.map((v,i)=>{
          if(v.box_num == item.box_num){
            this.box_info.splice(i,1)
          }
        })
        }).catch(() => {
          // on cancel
        });
      },
      addBox(){
        if(this.box_num.length != 11){
          this.$dialog({
            title: '',
            message: '11!' 
          });
        }else{
          if(this.box_info.length == 0){
            this.box_info.push({
              id: this.box_id,
              box_num: this.box_num,
              'abnormal_situation': this.abnormal_situation,
              'show_detail':false
            })
          }else{
            let have = false;
            this.box_info.find(v => {
              if(v.box_num == this.box_num){
                have = true
                this.$dialog({
                  title: '',
                  message: '!' 
                });
              }
            })
            if(!have){
              this.box_info.push({
                id: '',
                box_num: this.box_num,
                'abnormal_situation': this.abnormal_situation,
                'show_detail':false
              })
            }
          }
        }
        this.box_num = 'TBJU190';
      },
      saveTravel(){
        if(this.handle_station == '' || this.box_num == ''){
          this.$dialog({
            title: '',
            message: ''
          });
        }else if(this.box_info.length == 0){
          this.$dialog({
            title: '',
            message: ''
          });
        }else{
          if(this.img_info.length == 0 && this.img_list.length == 0){
            this.$toast('');
          }else{
            var formData = new FormData();
            formData.append('channel', 'app')
            formData.append('list_id', this.list_id)
            formData.append('list_type', '5')
            formData.append('box_num', this.box_num)
            formData.append('handle_station', this.handle_station)
            formData.append('operation_time', this.operation_time)
            formData.append('box_info', JSON.stringify(this.box_info))
            for(var i=0;i<this.img_info.length;i++){
              formData.append('file'+i,this.img_info[i].file,this.img_info[i].file.name)
            }
            this.$file.post('http://192.168.31.62:81/information/save_operation_list',formData,{
              headers: {
                'Content-Type': 'multipart/form-data',
                'Token': localStorage.getItem('token'),
              }
            }).then(res => {
              if(res.data.code == 200){
                this.$toast('');
                this.list_id = res.data.result;
                this.getOperationDetail();
                this.showAdd = false
              }else if(res.data.code == '401'){
                this.$dialog.alert({
                  title: '',
                  message: res.data.reason
                })
                this.$router.push({
                  path: './me'
                })
              }
            }).catch(err => {
              console.log(err)
            })
          }
        }
      },
      getOperationDetail(){
        this.$http.post('http://192.168.31.62:81/information/operation_list_details',{
          channel: 'app',
          list_id: this.list_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            this.img_list = res.data.result.img;
            this.box_num = res.data.result.operation[0].box_num
            this.handle_station = res.data.result.operation[0].handle_station
            this.operation_time = formatShowTime(res.data.result.operation[0].operation_time);          
            res.data.result.box.map(v => {
              v.show_detail = false
            })
            this.box_info = res.data.result.box;
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      cancel(){
        this.showScan = false;
        this.closeScan()
        this.cancelScan()
        scan = null
      },
      // 
      startRecognize () {     
        this.showScan = true;   
        let that = this
        if (!window.plus) return
        scan = new plus.barcode.Barcode('bcid')
        scan.onmarked = onmarked
        scan.start()
        function onmarked (type, result, file) {
          switch (type) {
            case plus.barcode.QR:
              type = 'QR'
              break
            case plus.barcode.EAN13:
              type = 'EAN13'
              break
            case plus.barcode.EAN8:
              type = 'EAN8'
              break
            default:
              type = '' + type
              break
          }
          result = result.replace(/\n/g, '')
          that.box_num = result
          that.showScan = false;
          that.closeScan()
          that.cancelScan()
          scan = null
        }
      },
      // 
      startScan () {
        
        if (!window.plus) return
        scan.start()
      },
      // 
      cancelScan () {
        if (!window.plus) return
        scan.cancel()
      },
      // 
      closeScan () {
        if (!window.plus) return
        scan.close()
      },
    },
    mounted () {
      this.list_id = this.$route.query.list_id
      if(this.list_id == undefined){
        this.list_id = '';
        this.showAdd = true
      }else{
        this.getOperationDetail();
      }
    },
  }
</script>

<style scoped>
#washBox{
  width: 100%;
  min-height: 100%;
  padding-top: 46px;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top *{
  color: white!important;
}
.box-list{
  margin: 10px;
  padding: 0 10px;
  background-color: white;
  color: #555555;
  font-size: 12px;
  display: block!important;
}
.box-list-title{
  border-bottom: 1px solid #dddddd;
}
.box-list>div{
  width: 100%;
  display: flex;
  justify-content:space-between;
  padding: 10px 0;
}
.box-list>div>span:first-child{
  min-width: 25%;
}
.box-list-delete{
  color: red;
  margin-left: 10px;
}
.edit{
  margin-left: 10px;
}
.add-box{
  background-color: white;
}
.field_title{
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  background-color: white;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
.box-op button{
  padding: 0 5px!important;
}
/*  */
.infoItem{
  border-top: 1px solid #eeeeee;
  font-size: 12px;
  padding: 0 10px;
  color: #555555;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  background-color: white;
}
.van-uploader{
  padding: 10px 16px;
}
.img-list{
  display: flex;
  padding: 10px 16px;
}
.img-list>div{
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 8px 8px 0;
}
.img-list>div img{
  width: 100%;
  height: 100%;
}
.img-list>div i{
  top: -8px;
  right: -8px;
  position: absolute;
  color: #969799;
}


/*  */
.scan {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}
.toTop{
  z-index: 100000;
}
.scan #bcid {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 3rem;
  text-align: center;
  color: #fff;
  background: #ccc;
}
#bcid .top{
  z-index: 1000000!important;
}
.scan footer {
  position: absolute;
  left: 0;
  bottom: 1rem;
  height: 2rem;
  line-height: 2rem;
  z-index: 2;
}
</style>