<template>
  <div id="move-inside">
    <van-nav-bar class="top"
      fixed
      title=""
      left-text=""
      :right-text="showAdd?'':''"
      left-arrow
      @click-left="goBack"
      @click-right="change"
    />
    <div class="add" v-show="showAdd">
      <van-field v-model="handle_station" required center clearable right-icon="question-o" label="" placeholder="" @click-right-icon="$toast('')">
        <van-button slot="button" :disabled="handle_station.length>1?false:true" size="small" type="primary" is-link @click="choiceHandleStation"></van-button>
      </van-field>
      <van-field
        v-model="operation_time"
        required
        disabled
        label=""
        placeholder=""
        @click="choiceMoveTime"
      />
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.box_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
            <van-icon name="edit" class="edit" @click="editList(i)"/>
            <van-icon name="cross" class="box-list-delete" @click="deleteList(i)" />
          </span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.original_position}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.move_position}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.move_reason}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>  
          <span>{{i.set_temp}}</span>  
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.open_temp}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.box_type == '1' ? '' : ''}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.box_status == '1' ? '' : ''}}</span>
        </div>
      </div>
  
      <div class="add-box">
        <h3></h3>
        <van-field v-model="box_num" required clearable label="" placeholder="" right-icon="qr" @click-right-icon="startRecognize()"/>
        <van-field v-model="original_position" clearable label="" placeholder=""/>
        <van-field v-model="move_position" clearable label="" placeholder=""/>
        <van-field v-model="move_reason" clearable label="" placeholder=""/>
        <van-field v-model="set_temp" clearable label="" placeholder=""/>
        <van-field v-model="open_temp" clearable label="" placeholder=""/>
        <div class="van-cell van-cell--center van-field">
          <div class="van-cell__title van-field__label">
            <span></span>
          </div>
          <div class="van-cell__value">
            <van-radio-group v-model="box_type" class="door_loadingType">
              <van-radio name="1"></van-radio>
              <van-radio name="2"></van-radio>
            </van-radio-group>
          </div>
        </div>
        <div class="van-cell van-cell--center van-field">
          <div class="van-cell__title van-field__label">
            <span></span>
          </div>
          <div class="van-cell__value">
            <van-radio-group v-model="box_status" class="door_loadingType">
              <van-radio name="1"></van-radio>
              <van-radio name="2"></van-radio>
            </van-radio-group>
          </div>
        </div>
        <div class="box-op">
          <van-button plain hairline round size="mini" type="danger" @click="addBox"></van-button>
        </div>
      </div>
    </div>
    <div class="show" v-show="!showAdd">
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{handle_station}}</span>
      </div>
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{operation_time}}</span>
      </div>
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.box_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
          </span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.original_position}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.move_position}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.move_reason}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>  
          <span>{{i.set_temp}}</span>  
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.open_temp}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.box_type == '1' ? '' : ''}}</span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.box_status == '1' ? '' : ''}}</span>
        </div>
      </div>
      <div class="box-op">
        <van-button plain hairline round size="mini" type="danger" @click="deleteYard"></van-button>
      </div>
    </div>
    <!--  -->
    <div :class="[showScan ? 'toTop':'', 'scan']">
      <div id="bcid">
        <van-nav-bar class="top"
          fixed
          title=""
          left-text=""
          left-arrow
          @click-left="cancel"
        />
        <div style="height:40%"></div>
        <p class="tip">......</p>
      </div>
    </div>
    <!--  -->
    <van-popup v-model="showHandleStation" position="bottom">
      <van-picker
        :columns="handle_stations"
        show-toolbar
        title=""
        @cancel="cancelHandleStation"
        @confirm="confirmHandleStation" />
    </van-popup>
    <!--  -->
    <van-popup v-model="showMoveTime" position="bottom">
      <van-datetime-picker
        v-model="moveTime"
        type="datetime"
        @cancel="cancelMoveTime"
        @confirm="confirmMoveTime"
      />
    </van-popup>
  </div>
</template>

<script>
let scan = null
import stationList from '../js/stationData.js';
import {formatDate } from '../js/formattime.js';
import {formatShowTime } from '../js/formatShowTime';
  export default {
    data() {
      return {
        list_id: '', //id
        showAdd: false, //
        stationList: stationList, //
        handle_station: '', //
        handle_stations: [], //
        showHandleStation: false, //
        operation_time: '', //
        moveTime: new Date(), //
        showMoveTime: false, //
        original_position: '',
        box_id: '', //id
        box_num: 'TBJU190', //
        box_type: '', //
        box_status: '', //
        original_position: '', //
        move_position: '', //
        move_reason: '', //
        set_temp: '', //
        open_temp: '', //
        box_info: [], //
        showScan: false
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      goBack() {
        history.go(-1)
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min  
      },
      change(){
        if(this.showAdd == false){
          this.showAdd = true
        }else{
          this.saveTravel()
        }
      },
      editList(item){
        this.box_id = item.box_id
        this.box_num = item.box_num
        this.box_type = item.box_type
        this.box_status = item.box_status
        this.original_position = item.original_position
        this.move_position = item.move_position
        this.move_reason = item.move_reason
        this.set_temp = item.set_temp
        this.open_temp = item.open_temp
        this.box_info.map((v,i)=>{
          if(v.box_num == item.box_num){
            this.box_info.splice(i,1)
          }
        })
      },
      // 
      choiceHandleStation(){
        this.handle_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.handle_station.toUpperCase()) != -1){
            this.handle_stations.push(v);
          }
        })
        this.showHandleStation = true;
      },
      cancelHandleStation(){
        this.showHandleStation = false;
      },
      confirmHandleStation(value){
        if(value.length != 0){
          this.handle_station = value.text;
        }
        this.showHandleStation = false;
      },
      // 
      choiceMoveTime(){
        this.showMoveTime = true
      },
      cancelMoveTime(){
        this.showMoveTime = false
      },
      confirmMoveTime(){
        this.showMoveTime = false;
        this.operation_time = this.timeFormat(this.moveTime)
      },
      // 
      choiceCloseTime(){
        this.showCloseTime = true
      },
      cancelCloseTime(){
        this.showCloseTime = false
      },
      confirmCloseTime(){
        this.showCloseTime = false;
        this.close_time = this.timeFormat(this.closeTime)
      },
      showDetail(item){
        this.box_info.find(v=>{v.box_num == item.box_num ? v.show_detail = !v.show_detail : ''})
      },
      // 
      deleteList(item){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/delete_box_info',{
            'channel': 'app',
            'box_id': item.box_id
          },{
            headers: {
              'Token': localStorage.getItem('token'),
            }
          })
        }).then(res => {
          if(res.data.code == 200){
            this.$toast('');
            this.box_info.map(v => {
              if(v.box_id == item.box_id){
                this.box_info.splice(i,1)
              }
            })
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      addBox(){
        if(this.box_num.length != 11){
          this.$dialog({
            title: '',
            message: '11!' 
          });
        }else{
          if(this.box_info.length == 0){
            this.box_info.push({
              'box_id': this.box_id,
              'box_num': this.box_num,
              'original_position': this.original_position,
              'move_position': this.move_position,
              'move_reason': this.move_reason,
              'set_temp': this.set_temp,
              'open_temp': this.open_temp,
              'box_type': this.box_type,
              'box_status': this.box_status,
              'show_detail':false
            })
          }else{
            let have = false;  
            this.box_info.find((v,i)=>{
              if(v.box_num == this.box_num){
                have = true
                this.$dialog({
                  title: '',
                  message: '!' 
                });
              }
            })
            if(!have){
              this.box_info.push({
                'box_id': this.box_id,
                'box_num': this.box_num,
                'original_position': this.original_position,
                'move_position': this.move_position,
                'move_reason': this.move_reason,
                'set_temp': this.set_temp,
                'open_temp': this.open_temp,
                'box_type': this.box_type,
                'box_status': this.box_status,
                'show_detail':false
              })
            }
          }
        }
        this.box_num = 'TBJU190',
        this.box_id = '';
      },
      saveTravel(){
        if(this.handle_station == '' || this.operation_time == ''){
          this.$dialog({
            title: '',
            message: ''
          });
        }else if(this.box_info.length == 0){
          this.$dialog({
            title: '',
            message: ''
          });
        }else{
          this.$http.post('http://192.168.31.62:81/information/save_operation_list',{
            channel: 'app',
            list_id: this.list_id,
            list_type: 1,
            handle_station: this.handle_station,
            operation_time: this.operation_time,
            box_info: JSON.stringify(this.box_info)
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == 200){
              this.$toast('');
              this.list_id = res.data.result;
              this.getOperationDetail();
              this.showAdd = false
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }else{
              this.$dialog.alert({
                title: '',
                message: ''+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      getOperationDetail(){
        this.$http.post('http://192.168.31.62:81/information/operation_list_details',{
          channel: 'app',
          list_id: this.list_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            this.handle_station = res.data.result.operation[0].handle_station;
            this.operation_time = formatShowTime(res.data.result.operation[0].operation_time);
            res.data.result.box.map(v => {
              v.show_detail = false
            })
            this.box_info = res.data.result.box;
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      cancel(){
        this.showScan = false;
        this.closeScan()
        this.cancelScan()
        scan = null
      },
      // 
      startRecognize () {     
        this.showScan = true;   
        let that = this
        if (!window.plus) return
        scan = new plus.barcode.Barcode('bcid')
        scan.onmarked = onmarked
        scan.start()
        function onmarked (type, result, file) {
          switch (type) {
            case plus.barcode.QR:
              type = 'QR'
              break
            case plus.barcode.EAN13:
              type = 'EAN13'
              break
            case plus.barcode.EAN8:
              type = 'EAN8'
              break
            default:
              type = '' + type
              break
          }
          result = result.replace(/\n/g, '')
          that.box_num = result
          that.showScan = false;
          that.closeScan()
          that.cancelScan()
          scan = null
        }
      },
      // 
      startScan () {        
        if (!window.plus) return
        scan.start()
      },
      // 
      cancelScan () {
        if (!window.plus) return
        scan.cancel()
      },
      // 
      closeScan () {
        if (!window.plus) return
        scan.close()
      },
      // 
      deleteYard(){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/operation_list_delete',{
            channel: 'app',
            list_id: this.list_id
          },{
             headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('');
              this.$router.push({
                path: './transport'
              })
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './yard'
              })
            }else{
              this.$dialog.alert({
                title: '',
                message: ''+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          });
        }).catch()
      }
    },
    mounted () {
      this.list_id = this.$route.query.list_id
      if(this.list_id == undefined){
        this.list_id = '';
        this.showAdd = true
      }else{
        this.getOperationDetail()
      }
    },
  }
</script>

<style scoped>
#move-inside{
  width: 100%;
  min-height: 100%;
  padding-top: 46px;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top *{
  color: white!important;
}
.box-list{
  margin: 10px;
  padding: 0 10px;
  background-color: white;
  color: #555555;
  font-size: 12px;
  display: block!important;
}
.box-list-title{
  border-bottom: 1px solid #dddddd;
}
.box-list>div{
  width: 100%;
  display: flex;
  justify-content:space-between;
  padding: 10px 0;
}
.box-list>div>span:first-child{
  min-width: 25%;
}
.box-list-delete{
  color: red;
  margin-left: 10px;
}
.edit{
  margin-left: 10px;
}
.add-box{
  background-color: white;
}
.add-box h3{
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
}
.door_loadingType.van-radio-group{
  display: flex;
  justify-content: space-around;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
.box-op button{
  padding: 0 5px!important;
}
/*  */
.infoItem{
  border-top: 1px solid #eeeeee;
  font-size: 12px;
  padding: 0 10px;
  color: #555555;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  background-color: white;
}
/*  */
.scan {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}
.toTop{
  z-index: 100000;
}
.scan #bcid {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 3rem;
  text-align: center;
  color: #fff;
  background: #ccc;
}
#bcid .top{
  z-index: 1000000!important;
}
.scan footer {
  position: absolute;
  left: 0;
  bottom: 1rem;
  height: 2rem;
  line-height: 2rem;
  z-index: 2;
}
</style>