<template>
  <div id="app">   
    <router-view/>
    <van-tabbar route id="tabbar" v-show="$route.meta.showTab">
      <van-tabbar-item icon="logistics" to="/transport">
        
      </van-tabbar-item>
      <van-tabbar-item icon="points" to="/yard" >
        
      </van-tabbar-item>
      <van-tabbar-item icon="wap-home-o" to="/home" >
        
      </van-tabbar-item>
      <van-tabbar-item icon="brush-o" to="/damage" >
        
      </van-tabbar-item>
      <van-tabbar-item icon="contact" to="/me">
        
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      token: this.$store.state.userInfo.token
    }
  },
  methods: {    
    checkLogin(){
      this.$http.post('http://192.168.31.62:81/information/user_login_info',{
        channel: 'app'
      },{
        headers: {'Token': localStorage.getItem('token')}
      }).then(res => {
        if(res.data.code == 200){
          if(this.$router.history.current.name != 'home'){
            this.$router.push({
              path: './home'
            })
          }
        }else{
          localStorage.removeItem('userInfo');
          this.$router.push({
            path: './Me'
          })
        }
      })
    }
  },
  mounted () {
    if(localStorage.getItem('token') == null || localStorage.getItem('token') == ''){
      if(this.$router.history.current.name != 'me'){
        this.$router.push({
          path: './Me'
        })
      }
    }else{
      this.checkLogin()
    }
  },
}
</script>

