<template>
  <div id="me">
    <div class="login" v-show="!userInfo.token">
      <van-field
        v-model="username"
        required
        clearable
        label=""
        placeholder=""
      />
      <van-field
        v-model="password"
        type="password"
        clearable
        label=""
        placeholder=""
        required
      />
      <div class="box-op">
        <van-button plain round type="primary" size="mini" @click="login"></van-button>
      </div>
    </div>
    <div class="login" v-show="userInfo.token">
      <div class="infoItem">
        <span></span>
        <span>{{userInfo.user_name}}</span>
      </div>
      <div class="infoItem">
        <span></span>
        <span>{{userInfo.real_name}}</span>
      </div>
      <div class="infoItem">
        <span></span>
        <span>{{userInfo.tel}}</span>
      </div>
      <div class="infoItem">
        <span></span>
        <span>
          {{version}}
          <span style="color:red">{{upgrade?'':''}}</span>
        </span>
      </div>
      <div class="box-op">
        <van-button plain round type="primary" size="mini" @click="logout"></van-button>
      </div>
    </div>
    
  </div>
</template>

<script>
  export default {
    data() {
      return {
        username: '',
        password: '',
        userInfo: {},
        version: 'v1.0',
        upgrade: false
      }
    },
    methods: {
      login() {
        if(this.username == ''){
          this.$dialog.alert({
            title: '',
            message: ''
          })
        }else if(this.password == ''){
          this.$dialog.alert({
            title: '',
            message: ''
          })
        }else{
          this.$http.post('http://192.168.31.62:81/welcome/user_login',{
            userName: this.username,
            userPassword: this.$md5(this.password),
            channel: 'app',
            // uuid: window.plus.device.uuid
            uuid: '869324020010197,869324020210195'
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast.success('');
              this.userInfo = res.data.result;
              localStorage.setItem('userInfo',JSON.stringify(res.data.result));
              localStorage.setItem('token',res.data.result.token);
              this.$router.push({
                path: './home'
              })  
            }else{
              this.$dialog.alert({
                title: '',
                message: ''+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      getV(){
        this.$http.post('http://192.168.31.62:81/information/reminder_version',{
          channel: 'app'
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            if(res.data.result == this.version){
              this.upgrade = false;
            }else{
              this.upgrade = true
            }
          }
        })
      },
      logout(){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/handle_user_login_out',{
            'channel': 'app'            
          },{
            headers: {
              'Token': localStorage.getItem('token'),
            }
          }).then(res => {
            if(res.data.code == 200){
              this.$toast('');
              window.localStorage.setItem('userInfo', '');
              window.localStorage.removeItem('token', '');
              this.userInfo = {};
            }
          })
        })
      }
    },
    mounted () {
      if(localStorage.getItem('token') && localStorage.getItem('userInfo')){
        this.userInfo = JSON.parse(localStorage.getItem('userInfo'));
        this.getV()
      }
    },
  }
</script>

<style scoped>
#me{
  width: 100%;
  min-height: 100%;
  background-image: url('../assets/home-bg.png');
  background-size: 100% 100%;  
  overflow: hidden;
}
.login{
  width: 80%;
  padding: 0px 10px;
  margin: 20vh auto 0;
  background: rgba(255,255,255,0.4);
  box-shadow: 0 0 10px #ffffff;
}
.login *{
  background: transparent;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
.box-op button{
  padding: 0 5px;
}
/*  */
.infoItem{
  /* border-top: 1px solid #eeeeee; */
  font-size: 12px;
  padding: 0 10px;
  color: #555555;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  /* background-color: white; */
}
</style>