<template>
  <div id="home">
    <div class="topBg">
      <h2></h2>
    </div>
    <van-grid clickable :column-num="5">
      <van-grid-item text="" to="/newway">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="home-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/moveInside">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="home-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/oil">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="home-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/washbox">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="home-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/NewDamage">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="home-tab"></span>
      </van-grid-item>
    </van-grid>
    <div class="list"> 
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="getWaybillList"
      >
        <van-cell class="list-item"
          v-for="item in list"
          :key="item.travel_id"
        >
          <h4>
            {{item.title}}
            <router-link :to="{name:'newbillDetail',query: {travel_id:item.travel_id}}">
              <van-icon name="arrow" />
            </router-link>
          </h4>
          <div class="list-item-content">
            <div class="travel">
              <div>
                <p>{{item.loading_branch}}{{item.leave_station}}</p>
                <span>{{item.departure_time | formatDate}}</span>
              </div>
              <div></div>
              <div>
                <p>{{item.arrave_branch	}}{{item.arrival_station}}</p>
                <span>{{item.end_time | formatDate}}</span>
              </div>
            </div>
            <div class="goods">
              <p>{{item.goods_name}}</p>
              <p>{{item.set_temp}}</p>
            </div>
          </div>
          <van-tag plain round type="danger" v-for="i in item.status">{{i}}</van-tag>
        </van-cell>
      </van-list>
    </div>
  </div>
</template>

<script>
import {formatDate } from '../js/formattime.js';
export default {
  data() {
    return {
      list: [],
      loading: false,
      finished: false
    }
  },
  filters: {
    formatDate(time) {
      if(time != 0 && time != null){
        var date = new Date( +time*1000);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }else{
        return ''
      }        
    }
  },
  methods: {
    getWaybillList(){
      this.$http.post('http://192.168.31.62:81/information/waybill_list',{
        channel: 'app'
      },{
        headers: {'Token': localStorage.getItem('token')}
      }).then(res => {
        if(res.data.code == '200'){
          this.list = res.data.result;
          this.finished = true
          this.loading = false;
        }else if(res.data.code == '401'){
          this.$dialog.alert({
            title: '',
            message: res.data.reason
          })
          this.$router.push({
            path: './me'
          })
        }else{
          this.$dialog.alert({
            title: '',
            message: ''+res.data.reason
          })
        }
      }).catch(err => {
        console.log(err)
      })
    }
  },
  mounted () {
    
  },
}
</script>

