<template>
  <div id="yard">
    <div class="top">
      <h2></h2>      
    </div>
    <van-grid class="opType" clickable :column-num="5">
      <van-grid-item text="" to="/moveInside">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="yard-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/deliver">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="yard-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/charge">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="yard-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/oil">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="yard-tab"></span>
      </van-grid-item>
      <van-grid-item text="" to="/washbox">
        <van-image src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=1868549669,2533900203&fm=26&gp=0.jpg" />
        <span class="yard-tab"></span>
      </van-grid-item>
    </van-grid>
    <div class="filter">
      <div></div>
      <div @click="showPopup">
        <van-icon name="filter-o" />
      </div>
    </div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="wayList">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onLoad"
        :offset="10"
      >
        <van-cell class="list-item"
          v-for="item in list"
          :key="item.list_id"
          >
          <h4>
            {{tabs[item.list_type-1].tab}}            
            <router-link :to="{name:tabUrl[item.list_type-1],query: {list_id:item.list_id}}">
              <van-icon name="arrow" />
            </router-link>
          </h4>
          <div class="list-item-content">
            <span></span>
            <span>{{item.handle_station}}</span>
          </div>
          <div class="list-item-content">
            <span></span>
            <span>{{item.operation_time | formatDate}}</span>
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>
    <!--  -->
    <van-popup v-model="showFilter" position="right" overlay-class="filterMask" class="filterPop">
      <div class="filterBtn">
        <van-button round plain type="danger" size="mini" v-for="item in tabs" :key="item.tab" @click="()=>{item.active = !item.active}" :class="item.active==true?'active':''">{{item.tab}}</van-button>
      </div>
      <div class="start-time">
        <span></span>
        <van-cell is-link @click="showStartTime" class="startChange">{{startTimeValue==''?'':startTimeValue}}</van-cell>
      </div>
      <div class="end-time">
        <span></span>
        <van-cell is-link @click="showEndTime" class="endChange">{{endTimeValue==''?'':endTimeValue}}</van-cell>
      </div>
      <div>
        <span></span>
        <input type="text" placeholder="" v-model="keyword">
      </div>
      <div class="filterBtn">
        <van-button round plain type="danger" size="mini" @click="reset"></van-button>        
        <van-button round plain type="danger" size="mini" @click="search"></van-button>        
      </div>
    </van-popup>
    <!--  -->
    <van-popup v-model="showStartTimePop" position="bottom">
      <van-datetime-picker
        @cancel="cancelStartData"
        @confirm="confirmStartData"
        @change="changeStartData"
        v-model="currentStartDate"
        type="date"
      />
    </van-popup>
    <!--  -->
    <van-popup v-model="showEndTimePop" position="bottom">
      <van-datetime-picker
        @cancel="cancelEndData"
        @confirm="confirmEndData"
        @change="changeEndData"
        v-model="currentEndDate"
        type="date"
        :min-date="currentStartDate"
      />
    </van-popup>
  </div>
</template>

<script>
import {formatDate } from '../js/formattime.js';
  export default {
    data() {
      return {
        currentPage: 0, //
        totalPage: 1, //
        showFilter: false,
        // tabs: ['','','','',''],
        tabs: [{'active':true,'tab':''},{'active':true,'tab':''},{'active':true,'tab':''},{'active':true,'tab':''},{'active':true,'tab':''},],
        tabUrl:['moveInside','deliver','charge','oil','washbox'],
        list_type: '',
        showStartTimePop: false,
        showEndTimePop: false,
        currentStartDate: new Date(),
        currentEndDate: new Date(),
        changeStartDate: new Date(),
        changeendDate: new Date(),
        startTimeValue: '',
        endTimeValue: '',
        keyword: '',
        list: [], //
        loading: false,
        finished: false,
        isLoading: false
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      showPopup() {
        this.showFilter = true
      },
      showStartTime(){
        this.showStartTimePop = true;
      },
      showEndTime(){
        this.showEndTimePop = true
      },
      changeStartData() { 
        this.changeStartDate = this.currentStartDate 
      },
      changeEndData() { 
        this.changeEndDate = this.currentEndDate 
      },
      confirmStartData() { 
        this.startTimeValue = this.timeFormat(this.currentStartDate);
        this.showStartTimePop = false;
      },
      confirmEndData() { 
        this.endTimeValue = this.timeFormat(this.currentEndDate);
        this.showEndTimePop = false;
      },
      cancelStartData(){
        this.showStartTimePop = false;
      },
      cancelEndData(){
        this.showEndTimePop = false;
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        return year + '-' + month + '-' + day        
      },
      reset(){
        this.startTimeValue = ''
        this.endTimeValue = ''
        this.keyword = ''
        this.tabs.map((item,index)=>{
          item.active = true
        })
      },
      search(){
        this.list = [];
        this.showFilter = false;
        this.currentPage = 0;
        this.onLoad()
      },
      onLoad(){
        this.list_type = '';
        this.tabs.map((item,index)=>{
          if(item.active == true){
            this.list_type+= (index+1)+',';
          }
        })
        if(this.list_type.length != 0){
          this.$http.post('http://192.168.31.62:81/information/all_operation_list_page',{
            channel: 'app',
            list_type: this.list_type,
            start_time: this.startTimeValue,
            end_time: this.endTimeValue,
            keywords: this.keyword,
            page:  ++this.currentPage
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == 200){
              if(res.data.result.total == 0){
                this.finished = true
                this.loading = false;
                this.isLoading = false;
              }else{
                this.totalPage = Math.ceil(+res.data.result.total/5);
                for(var i=0;i<res.data.result.operationInfo.length;i++){
                  this.list.push(res.data.result.operationInfo[i])
                }            
                if(this.currentPage == this.totalPage){
                  this.finished = true
                }else{
                  this.finished = false
                }
                // 
                this.loading = false;
                this.isLoading = false;
              }
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }else{
              this.$dialog.alert({
                title: '',
                message: ''+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }else{
          this.$dialog.alert({
            title: '',
            message: ''
          })
        }
      },
      onRefresh(){
        this.list = [];
        this.currentPage = 0;
        this.onLoad();
      }
    }
  }
</script>

<style scoped>
#yard{
  width: 100%;
  height: 100%;
  padding: 46px 0 50px 0;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top h2{
  color: white;
  text-align: center;
  font-size: 20px;
  font-weight: normal;
  line-height: 46px;
}
.opType{
  position: fixed;
  top: 46px;
  left: 0;
  z-index: 1000;
}
.yard-tab{
  font-size: 12px;
}
/*  */
.filter{
  font-size: 10px;
  height: 30px;
  line-height: 30px;
  text-align: left;
  padding: 0 10px;
  display: flex;  
  box-shadow: 0 2px 2px #eeeeee;
  background-color: white;
  justify-content: space-between;
  position: fixed;
  top: 142px;
  z-index: 100;
  width: 100%
}
.filter>div{
  display: flex; 
  align-items: center;
}
.filterPop{
  height: calc(100vh - 142px);
  width: 60%;
  top: calc(50% + 71px)!important;
  padding: 10px;
}
.filterMask{
  height: calc(100vh - 46px)!important;
  bottom: 0!important;
  top: auto!important;
}
.filterBtn{
  display: flex;
  justify-content: space-around!important;  
  flex-wrap: wrap;
}
.filterBtn button{
  font-size: 10px;
  margin: 5px 0;
}
.filterBtn button span{
  padding: 0 5px;
}
.filterBtn button.active{
  background-color: #ee0a24;
}
.filterBtn button.active span{
  color: white;
}
.filterPop>div{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-top: 1px solid #cccccc;
  font-size: 12px;
}
.filterPop>div:first-child{
  border: none;
}
.startChange,.endChange{
  padding: 0!important;
  width: 55%!important;
  font-size: 12px!important;
}
.filterPop input{
  border: none;
  width: 55%!important;
}
.list-item{
  margin: 10px;
  padding: 10px!important;
  width: auto!important;
}
.list-item-content{
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #dddddd;
  color: #555555;
}
/*  */
.wayList{
  padding-top: 126px;
}
.list-item h4{
  display: flex;
  justify-content: space-between;
}
</style>