<template>
  <div id="charge">
    <van-nav-bar class="top"
      fixed
      title=""
      left-text=""
      :right-text="showAdd?'':''"
      left-arrow
      @click-left="goBack"
      @click-right="change"
    />
    <div class="add" v-show="showAdd">
      <van-field v-model="handle_station" required center clearable right-icon="question-o" label="" placeholder="" @click-right-icon="$toast('')">
        <van-button slot="button" :disabled="handle_station.length>1?false:true" size="small" type="primary" is-link @click="choiceHandleStation"></van-button>
      </van-field>
      <van-field
        v-model="operation_time"
        disabled
        label=""
        placeholder=""
        @click="choiceChargeTime"
      />
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.case_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
            <van-icon name="edit" class="edit" @click="editList(i)"/>
            <van-icon name="cross" class="box-list-delete" @click="deleteList(i)" />
          </span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.set_temp}}</span>
        </div>
        <div v-show="i.show_detail">
          <span>h</span>
          <span>{{i.charging_time}}</span>
        </div>      
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.abnormal_situation}}</span>
        </div>   
      </div>
      <div class="add-box">
        <h3></h3>
        <van-field v-model="case_num" required clearable label="" placeholder=""/>  
        <van-field v-model="set_temp" clearable label="" placeholder=""/>        
        <van-field v-model="charging_time" clearable label="h" placeholder=""/>  
        <van-field
          v-model="abnormal_situation"
          rows="1"
          autosize
          label=""
          type="textarea"
          placeholder=""
        />
        <div class="box-op">
          <van-button plain hairline round size="mini" type="danger" @click="addBox"></van-button>
        </div>  
      </div>
    </div>
    <div class="show" v-show="!showAdd">
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{handle_station}}</span>
      </div>
      <div class="infoItem">
        <span class="field"></span>
        <span class="info">{{operation_time}}</span>
      </div>
      <div v-for="i in box_info" :key="i.box_num" class="box-list">
        <div :class="i.show_detail? 'box-list-title':''">
          <span>{{i.case_num}}</span>
          <span>
            <van-icon name="arrow-up" v-show="i.show_detail" @click="showDetail(i)" />
            <van-icon name="arrow-down" v-show="!i.show_detail" @click="showDetail(i)" />
          </span>
        </div>
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.set_temp}}</span>
        </div>
        <div v-show="i.show_detail">
          <span>h</span>
          <span>{{i.charging_time}}</span>
        </div>      
        <div v-show="i.show_detail">
          <span></span>
          <span>{{i.abnormal_situation}}</span>
        </div>   
      </div>
      <div class="box-op">
        <van-button plain hairline round size="mini" type="danger" @click="deleteYard"></van-button>
      </div>
    </div>
    <!--  -->
    <van-popup v-model="showHandleStation" position="bottom">
      <van-picker
        :columns="handle_stations"
        show-toolbar
        title=""
        @cancel="cancelHandleStation"
        @confirm="confirmHandleStation" />
    </van-popup>
    <!--  -->
    <van-popup v-model="showChargeTime" position="bottom">
      <van-datetime-picker
        v-model="chargeTime"
        type="datetime"
        @cancel="cancelChargeTime"
        @confirm="confirmChargeTime"
      />
    </van-popup>
  </div>
</template>

<script>
  import {formatShowTime } from '../js/formatShowTime';
  import {formatDate } from '../js/formattime.js';
  import stationList from '../js/stationData.js';
  export default {
    data() {
      return {
        list_id:'',
        showAdd: false, //
        stationList: stationList, //
        handle_station: '', //
        handle_stations: [], //
        showHandleStation: false, //
        operation_time: '', //
        chargeTime: new Date(), //
        showChargeTime: false, //
        case_num: '', //
        box_id: '',
        set_temp: '', //
        charging_time: '', //
        abnormal_situation: '', //
        box_info: [] //
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      goBack() {
        history.go(-1)
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let hour = time.getHours();
        let min = time.getMinutes();
        min = (min+'').length==1?'0'+min:min
        return year + '-' + month + '-' + day + ' ' + hour + ':' + min  
      },
      change(){
        if(this.showAdd == false){
          this.showAdd = true
        }else{
          this.saveTravel()
        }
      },
      showDetail(item){
        this.box_info.find(v=>{v.case_num == item.case_num ? v.show_detail = !v.show_detail : ''})
      },
      editList(item){
        this.box_id = item.box_id
        this.case_num = item.case_num
        this.set_temp = item.set_temp
        this.charging_time = item.charging_time
        this.abnormal_situation = item.abnormal_situation
        this.box_info.map((v,i)=>{
          if(v.box_num == item.box_num){
            this.box_info.splice(i,1)
          }
        })
      },
      // 
      choiceHandleStation(){
        this.handle_stations = [];
        this.stationList.find(v=>{
          if(v.pinyin.search(this.handle_station.toUpperCase()) != -1){
            this.handle_stations.push(v);
          }
        })
        this.showHandleStation = true;
      },
      cancelHandleStation(){
        this.showHandleStation = false;
      },
      confirmHandleStation(value){
        if(value.length != 0){
          this.handle_station = value.text;
        }
        this.showHandleStation = false;
      },
      // 
      choiceChargeTime(){
        this.showChargeTime = true
      },
      cancelChargeTime(){
        this.showChargeTime = false
      },
      confirmChargeTime(){
        this.showChargeTime = false;
        this.operation_time = this.timeFormat(this.chargeTime)
      },
      // 
      deleteList(item){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/delete_box_info',{
            'channel': 'app',
            'box_id': item.box_id
          },{
            headers: {
              'Token': localStorage.getItem('token'),
            }
          })
        }).then(res => {
          if(res.data.code == 200){
            this.$toast('');
            this.box_info.map(v => {
              if(v.box_id == item.box_id){
                this.box_info.splice(i,1)
              }
            })
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      addBox(){
        if(this.case_num == ''){
          this.$dialog({
            title: '',
            message: '!' 
          });
        }else if(this.box_info.length == 0){
          this.box_info.push({
            'box_id': this.box_id,
            'case_num': this.case_num,
            'set_temp': this.set_temp,
            'charging_time': this.charging_time,
            'abnormal_situation': this.abnormal_situation,
            'show_detail':false
          })
        }else{
          let have = false;
          this.box_info.find((v,i)=>{
            if(v.case_num == this.case_num){
              have = true
              this.$dialog({
                title: '',
                message: '!' 
              });
            }
            if(!have){
              this.box_info.push({
                'box_id': this.box_id,
                'case_num': this.case_num,
                'set_temp': this.set_temp,
                'charging_time': this.charging_time,
                'abnormal_situation': this.abnormal_situation,
                'show_detail':false
              })
            }
          })
        }
        this.case_num = '';
        this.box_id = ''
      },
      saveTravel(){
        if(this.handle_station == ''){
          this.$dialog({
            title: '',
            message: ''
          });
        }else if(this.box_info.length == 0){
          this.$dialog({
            title: '',
            message: ''
          });
        }else{
          this.$http.post('http://192.168.31.62:81/information/save_operation_list',{
            channel: 'app',
            list_type: 3,
            list_id: this.list_id,
            handle_station: this.handle_station,
            operation_time: this.operation_time,
            box_info: JSON.stringify(this.box_info)
          },{
            headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == 200){
              this.$toast('');
              this.list_id = res.data.result;
              this.getOperationDetail();
              this.showAdd = false
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './me'
              })
            }else{
               this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      getOperationDetail(){
        this.$http.post('http://192.168.31.62:81/information/operation_list_details',{
          channel: 'app',
          list_id: this.list_id
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            this.handle_station = res.data.result.operation[0].handle_station;
            this.operation_time = formatShowTime(res.data.result.operation[0].operation_time);
            res.data.result.box.map(v => {
              v.show_detail = false
            })
            this.box_info = res.data.result.box;
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 
      deleteYard(){
        this.$dialog.confirm({
          title: '',
          message: ''
        }).then(() => {
          this.$http.post('http://192.168.31.62:81/information/operation_list_delete',{
            channel: 'app',
            list_id: this.list_id
          },{
             headers: {'Token': localStorage.getItem('token')}
          }).then(res => {
            if(res.data.code == '200'){
              this.$toast('');
              this.$router.push({
                path: './transport'
              })
            }else if(res.data.code == '401'){
              this.$dialog.alert({
                title: '',
                message: res.data.reason
              })
              this.$router.push({
                path: './yard'
              })
            }else{
              this.$dialog.alert({
                title: '',
                message: ''+res.data.reason
              })
            }
          }).catch(err => {
            console.log(err)
          });
        }).catch()
      }
    },
    mounted () {
      this.list_id = this.$route.query.list_id
      if(this.list_id == undefined){
        this.list_id = '';
        this.showAdd = true
      }else{
        this.getOperationDetail()
      }
    },
  }
</script>

<style scoped>
#charge{
  width: 100%;
  min-height: 100%;
  padding-top: 46px;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top *{
  color: white!important;
}
.box-list{
  margin: 10px;
  padding: 0 10px;
  background-color: white;
  color: #555555;
  font-size: 12px;
  display: block!important;
}
.box-list-title{
  border-bottom: 1px solid #dddddd;
}
.box-list>div{
  width: 100%;
  display: flex;
  justify-content:space-between;
  padding: 10px 0;
}
.box-list>div>span:first-child{
  min-width: 25%;
}
.box-list-delete{
  color: red;
  margin-left: 20px;
}
.edit{
  margin-left: 20px;
}
.add-box{
  background-color: white;
}
.add-box h3{
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
}
.box-op{
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
}
.box-op button{
  padding: 0 5px!important;
}
/*  */
.infoItem{
  border-top: 1px solid #eeeeee;
  font-size: 12px;
  padding: 0 10px;
  color: #555555;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  background-color: white;
}
</style>