<template>
  <div class="transport">
    <div class="top">
      <h2></h2>
      <router-link to="/Newway" id="newWay">
        <van-icon name="plus" />
      </router-link>
    </div>
    <div class="filter">
      <div>
        <van-icon name="arrow" />
        {{activeTab}} {{startTimeValue}} {{startTimeValue!=''&&endTimeValue!=''?' ~ ':''}} {{endTimeValue}}
      </div>
      <div @click="showPopup">
        <van-icon name="filter-o" />
      </div>
    </div>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="wayList">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onLoad"
        :offset="10"
      >
        <van-cell class="list-item"
          v-for="item in list"
          :key="item.travel_id"
          >
          <h4>
            {{item.title}}
            <router-link :to="{name:'newbillDetail',query: {travel_id:item.travel_id}}">
              <van-icon name="arrow" />
            </router-link>
          </h4>
          <div class="list-item-content">
            <div class="travel">
              <div>
                <p>{{item.loading_branch}}{{item.leave_station}}</p>
                <span>{{item.departure_time | formatDate}}</span>
              </div>
              <div></div>
              <div>
                <p>{{item.arrave_branch	}}{{item.arrival_station}}</p>
                <span>{{item.end_time}}</span>
              </div>
            </div>
            <div class="goods">
              <p>{{item.goods_name}}</p>
              <p>{{item.set_temp}}</p>
            </div>
          </div>
          <van-tag plain round type="danger" v-for="i in item.status">{{i}}</van-tag>
        </van-cell>
      </van-list>
    </van-pull-refresh>
    <!--  -->
    <van-popup v-model="showFilter" position="right" overlay-class="filterMask" class="filterPop">
      <div class="filterBtn">
        <van-button round plain type="danger" size="mini" v-for="(tab,i) in tabs" :key="tab" @click="()=>{activeTab = tab;activeTabIndex = i}" :class="tab==activeTab?'active':''">{{tab}}</van-button>        
      </div>
      <div class="start-time">
        <span></span>
        <van-cell is-link @click="showStartTime" class="startChange">{{startTimeValue==''?'':startTimeValue}}</van-cell>
      </div>
      <div class="end-time">
        <span></span>
        <van-cell is-link @click="showEndTime" class="endChange">{{endTimeValue==''?'':endTimeValue}}</van-cell>
      </div>
      <div>
        <span></span>
        <input type="text" placeholder="" v-model="keyword">
      </div>
      <div class="filterBtn">
        <van-button round plain type="danger" size="mini" @click="reset"></van-button>        
        <van-button round plain type="danger" size="mini" @click="search"></van-button>        
      </div>
    </van-popup>
    <!--  -->
    <van-popup v-model="showStartTimePop" position="bottom">
      <van-datetime-picker
        @cancel="cancelStartData"
        @confirm="confirmStartData"
        @change="changeStartData"
        v-model="currentStartDate"
        type="date"
      />
    </van-popup>
    <!--  -->
    <van-popup v-model="showEndTimePop" position="bottom">
      <van-datetime-picker
        @cancel="cancelEndData"
        @confirm="confirmEndData"
        @change="changeEndData"
        v-model="currentEndDate"
        type="date"
        :min-date="currentStartDate"
      />
    </van-popup>
  </div> 
</template>

<script>
import {formatDate } from '../js/formattime.js';
  export default {
    data() {
      return {
        currentPage: 0, //
        totalPage: 1, //
        showFilter: false,
        showStartTimePop: false,
        tabs: ['','',''],
        activeTab: null,
        activeTabIndex: 0, 
        showStartTimePop: false,
        showEndTimePop: false,
        currentStartDate: new Date(),
        currentEndDate: new Date(),
        changeStartDate: new Date(),
        changeendDate: new Date(),
        startTimeValue: '',
        endTimeValue: '',
        keyword: '',
        list: [],
        loading: false,
        finished: false,
        isLoading: false
      }
    },
    filters: {
      formatDate(time) {
        if(time != 0 && time != null){
          var date = new Date( +time*1000);
          return formatDate(date, 'yyyy-MM-dd hh:mm');
        }else{
          return ''
        }        
      }
    },
    methods: {
      showPopup() {
        this.showFilter = true
      },
      showStartTime(){
        this.showStartTimePop = true;
      },
      showEndTime(){
        this.showEndTimePop = true
      },
      changeStartData() { 
        this.changeStartDate = this.currentStartDate 
      },
      changeEndData() { 
        this.changeEndDate = this.currentEndDate 
      },
      confirmStartData() { 
        this.startTimeValue = this.timeFormat(this.currentStartDate);
        this.showStartTimePop = false;
      },
      confirmEndData() { 
        this.endTimeValue = this.timeFormat(this.currentEndDate);
        this.showEndTimePop = false;
      },
      cancelStartData(){
        this.showStartTimePop = false;
      },
      cancelEndData(){
        this.showEndTimePop = false;
      },
      timeFormat(time) {
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        return year + '-' + month + '-' + day        
      },
      reset(){
        this.activeTab = null
        this.startTimeValue = ''
        this.endTimeValue = ''
        this.keyword = ''
      },
      search(){
        this.list = [];
        this.showFilter = false;
        this.currentPage = 0;
        this.onLoad()
      },
      onLoad() {
        // this.list = [];        
        this.$http.post('http://192.168.31.62:81/information/waybill_list_page',{
          channel: 'app',
          type: this.activeTabIndex+1,
          start_time: this.startTimeValue,
          end_time: this.endTimeValue,
          keywords: this.keyword,
          page: ++this.currentPage
        },{
          headers: {'Token': localStorage.getItem('token')}
        }).then(res => {
          if(res.data.code == 200){
            if(res.data.result.total == 0){
              this.finished = true
              this.loading = false;
              this.isLoading = false;
            }else{
              this.totalPage = Math.ceil(+res.data.result.total/5);
              for(var i=0;i<res.data.result.travelInfo.length;i++){
                this.list.push(res.data.result.travelInfo[i])
              }            
              if(this.currentPage == this.totalPage){
                this.finished = true
              }else{
                this.finished = false
              }
              // 
              this.loading = false;
              this.isLoading = false;
            }
          }else if(res.data.code == '401'){
            this.$dialog.alert({
              title: '',
              message: res.data.reason
            })
            this.$router.push({
              path: './me'
            })
          }else{
            this.$dialog.alert({
              title: '',
              message: ''+res.data.reason
            })
          }
        }).catch(err => {
          console.log(err)
        }) 
      },
      onRefresh(){
        this.list = [];
        this.currentPage = 0;
        this.onLoad();
      }
    },
    mounted () {
      
    },
  }
</script>

<style scoped>
.transport{
  height: 100%;
}
.top{
  background-image: url('../assets/home-bg.png');
  height: 46px;
  background-position: center center;
  background-size: 100% auto; 
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
}
.top h2{
  color: white;
  text-align: center;
  font-size: 20px;
  font-weight: normal;
  line-height: 46px;
}
.filter{
  font-size: 10px;
  height: 30px;
  line-height: 30px;
  text-align: left;
  padding: 0 10px;
  display: flex;  
  box-shadow: 0 2px 2px #eeeeee;
  background-color: white;
  justify-content: space-between;
  position: fixed;
  top: 46px;
  z-index: 100;
  width: 100%
}
.filter>div{
  display: flex; 
  align-items: center;
}
.filterPop{
  height: calc(100vh - 46px);
  width: 60%;
  top: calc(50% + 23px)!important;
  padding: 10px;
}
.filterMask{
  height: calc(100vh - 46px)!important;
  bottom: 0!important;
  top: auto!important;
}
.filterBtn{
  display: flex;
  justify-content: space-between;
}
.filterBtn button{
  font-size: 10px;
}
.filterBtn button.active{
  background-color: #ee0a24;
}
.filterBtn button.active span{
  color: white;
}
.filterPop>div{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  border-top: 1px solid #cccccc;
  font-size: 12px;
}
.filterPop>div:first-child{
  border: none;
}
.startChange,.endChange{
  padding: 0!important;
  width: 55%!important;
  font-size: 12px!important;
}
.filterPop input{
  border: none;
  width: 55%!important;
}
.wayList{
  padding-top: 74px;
}
#newWay{
  position: absolute;
  top: 0;
  right: 15px;
  color: white;
  line-height: 49px;
}
.list-item h4{
  display: flex;
  justify-content: space-between;
}
.list-item{
  margin: 10px;
  padding: 10px!important;
  width: auto!important;
}
.list-item-content{
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid black;
  margin-bottom: 5px;
}
.travel{
  width: 70%;
  display: flex;
  font-size: 12px;
}
.travel>div{
  text-align: center;
}
.travel>div *{
  white-space: nowrap;
}
.goods{
  text-align: center;
  font-size: 12px;
}
</style>